:root {
	--white: #FFFFFF;
	--black: #000000;
	--very-light-pink: #C7C7C7;
	--text-input-field: #F7F7F7;
	--hospital-green: #ACD9B2;
	--sm: 14px;
	--md: 16px;
	--lg: 18px;
}
body {
	margin: 0;
	font-family: 'Quicksand', sans-serif;
}
nav {
	box-sizing: border-box;
	position: fixed;
	display: flex;
	width: 100%;
	padding: 0 24px;
	justify-content: space-between;
	border-bottom: 1px solid var(--very-light-pink);
	background: var(--white);
	top:0;
}
.menu {
	display: none;
}
.logo {
	width: 100px;
}
.navbar_left ul,
.navbar_right ul {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 60px;
}
.navbar_left {
	display: flex;
}
.navbar_left ul {
	margin-left: 12px;
}
.navbar_left ul li a,
.navbar_right ul li a {
	text-decoration: none;
	color: var(--very-light-pink);
	border: 1px solid var(--white);
	padding: 8px;
	border-radius: 8px;
}
.navbar_left ul li a:hover,
.navbar_right ul li a:hover {
	border: 1px solid var(--hospital-green);
	color: var(--hospital-green);
}
.navbar_email {
	color: var(--very-light-pink);
	font-size: var(--sm);
	margin-right: 12px;
	cursor: pointer;
}
.navbar_shopping_cart {
	position: relative;
}
.navbar_shopping_cart div {
	width: 16px;
	height: 16px;
	background-color: var(--hospital-green);
	border-radius: 50%;
	font-size: var(--sm);
	font-weight: bold;
	position: absolute;
	top: -6px;
	right: -3px;
	display: flex;
	justify-content: center;
	align-items: center;
}
@media (max-width: 640px) {
	.menu {
		display: block;
	}
	.navbar_left ul {
		display: none;
	}
	.navbar-email {
		display: none;
	}
}